<script>
  import * as R from 'ramda'
  import Up from '../assets/up.png'
  import Down from '../assets/down.png'
  import Map from '../components/Map'
  import Fetch from "../util/Fetch";
  import http from "../util/http";

  export default {
    components: {
      Map: Map,
    },
    destroyed() {
    },
    methods: {
      chainRatio: function (item) {
        let result = parseInt(item.CheckResult)
        let standard = parseInt(item.StandardValue)
        return {
          value: Math.abs(((result - standard) / standard * 100)).toFixed(2) + "%",
          positive: result - standard > 0,
          img: result - standard > 0 ? Up : Down,
        }
      },
      async getAir(api, name) {
        let res = await Fetch(api);
        this.left_1 = res.data
        this.left_1_current = this.left_1_current ? this.left_1_current : res.data.length > 0 ? res.data[0].Id : 0;
        this.left_1_2_current = name
      },
      async getWater(api, name) {
        let res = await Fetch(api);
        this.left_2 = res.data
        this.left_2_current = this.left_2_current ? this.left_2_current : res.data.length > 0 ? res.data[0].Id : 0;
        this.left_2_2_current = name
      }
    },
    async mounted() {
      this.getAir(http.AirHourData_GetMonitoringPointData, "小时");
      this.getWater(http.WaterHourData_GetMonitoringPointData, "小时");
      let res = await Fetch(http.AirAlert);
      this.airPollutants = res.data;
      res = await Fetch(http.WaterAlert);
      this.waterPollutants = res.data;
    },
    data() {
      let data = []
      for (let i = 0; i <= 360; i++) {
        let t = i / 180 * Math.PI
        let r = Math.sin(2 * t) * Math.cos(2 * t)
        data.push([r, i])
      }
      return {
        airPollutants:[],
        waterPollutants:[],
        airType: [
          {
            name: '小时',
            api: http.AirHourData_GetMonitoringPointData
          }, {
            name: '日',
            api: http.AirHourData_GetMonitoringPointData
          }, {
            name: '月',
            api: http.AirHourData_GetMonitoringPointData
          }
        ],
        waterType: [
          {
            name: '小时',
            api: http.WaterHourData_GetMonitoringPointData
          }, {
            name: '日',
            api: http.WaterDayData_GetMonitoringPointData
          }, {
            name: '月',
            api: http.WaterMonthData_GetMonitoringPointData
          }
        ],
        left_1_current: 0,
        left_2_current: 0,
        left_1_2_current: "小时",
        left_2_2_current: "小时",
        left_1: [],
        left_2: [],
      }
    },
    computed: {
      "left_1_2": function () {
        let company = R.find(R.propEq("Id", this.left_1_current))(this.left_1);
        if (!company) return {};
        let {Data, ...others} = company;
        let left_1_2 = Data.map(v => ({
          ...v,
          ...others,
          value: v.Val,
        }));
        return {
          title: {
            text: '',
            left: 'center'
          },
          tooltip: {
            trigger: 'axis',
            padding: [2, 10],
            textStyle: {
              fontSize: 16
            },
            formatter([item]) {
              return `${item.data.CompanyName} <br/> ${item.marker} ${item.data.PointName} : ${item.data.value}`
            }
          },
          xAxis: [
            {
              type: 'category',
              boundaryGap: false,
              splitLine: {
                show: false,
              },
              axisTick: { // 刻度
                show: false,
              },
              data: left_1_2.map(v => v.Date)
            }
          ],
          grid: {
            left: '4%',
            top: '4%',
            right: '4%',
            bottom: '4%',
            containLabel: true
          },
          yAxis: [
            {
              type: 'value',
              splitLine: { //网格线
                show: false,
              },
              axisLine: { // 坐标轴
                show: false,
              },
              axisTick: { // 刻度
                show: false,
              },
            },
          ],
          series: [{
            type: 'line',
            symbol: 'circle',
            symbolSize: 4,
            smooth: true,
            animationDuration: 500,
            itemStyle: {
              normal: {
                color: '#1af1a9',
                borderWidth: 2,
                shadowBlur: 2,
                shadowColor: "rgba(0, 0, 0, .12)",
                shadowOffsetX: 2,
                shadowOffsetY: 2
              }
            },
            lineStyle: {
              normal: {
                color: "#1af1a9",
                width: 1,
                shadowColor: '#1af1a9',
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowOffsetY: 8
              }
            },
            data: left_1_2
          }]
        }
      },
      "left_2_2": function () {
        let company = R.find(R.propEq("Id", this.left_2_current))(this.left_2);
        if (!company) return {};
        let {Data, ...others} = company;
        let left_2_2 = Data.map(v => ({
          ...v,
          ...others,
          value: v.Val,
        }));
        return {
          title: {
            text: '',
            left: 'center'
          },
          tooltip: {
            trigger: 'axis',
            padding: [2, 10],
            textStyle: {
              fontSize: 16
            },
            formatter([item]) {
              return `${item.data.CompanyName} <br/> ${item.marker} ${item.data.PointName} : ${item.data.value}`
            }
          },
          xAxis: [
            {
              type: 'category',
              boundaryGap: false,
              splitLine: {
                show: false,
              },
              axisTick: { // 刻度
                show: false,
              },
              data: left_2_2.map(v => v.Date)
            }
          ],
          grid: {
            left: '4%',
            top: '4%',
            right: '4%',
            bottom: '4%',
            containLabel: true
          },
          yAxis: [
            {
              type: 'value',
              splitLine: { //网格线
                show: false,
              },
              axisLine: { // 坐标轴
                show: false,
              },
              axisTick: { // 刻度
                show: false,
              },
            },
          ],
          series: [{
            type: 'line',
            symbol: 'circle',
            symbolSize: 4,
            smooth: true,
            animationDuration: 500,
            itemStyle: {
              normal: {
                color: '#1af1a9',
                borderWidth: 2,
                shadowBlur: 2,
                shadowColor: "rgba(0, 0, 0, .12)",
                shadowOffsetX: 2,
                shadowOffsetY: 2
              }
            },
            lineStyle: {
              normal: {
                color: "#1af1a9",
                width: 1,
                shadowColor: '#1af1a9',
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowOffsetY: 8
              }
            },
            data: left_2_2
          }]
        }
      }
    },
  }
</script>

